.features-list {
  @extend .main-list;
  margin-bottom: 0;

  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 10px;
  width: 100%;

  @include m-b-down(960px) {
    grid-template-columns: repeat(2, 1fr);
    max-width: 600px;
  }

  @include m-b-mobile-lg-down {
    grid-template-columns: 1fr;
    grid-gap: 0;
  }

  &__item {
    height: 100%;

    img {
      max-width: 300px;
      height: auto;
      object-fit: contain;

      @include m-b-down(1140px) {
        max-width: 200px;
      }

      @include m-b-tablet-down {
        max-width: 200px;
      }

      @include m-b-mobile-lg-down {
        max-width: 280px;
      }
    }

    .lw-title-sm {
      font-size: 20px;
      text-align: center;
      margin-bottom: 0;

      @include m-b-down(1140px) {
        font-size: 18px;
      }

      @include m-b-tablet-down {
        font-size: 16px;
      }
    }
  }
}
